import {useNavigate} from "react-router-dom";
import {Nav, Col} from "react-bootstrap";

function Data(props) {
  let navigate = useNavigate();
  return (
    <>
      <Nav.Link
        onClick={() => {
          navigate("/Detail/" + props.i);
        }}
        className="c1"
      >
        <img alt="common" src={process.env.PUBLIC_URL + props.data1.imgUrl} width="80%" />
        <p className="title_price">{props.data1.price.toLocaleString("kr") + "원"}</p>
        <p className="title_txt">{props.data1.title}</p>
        <p className="title_content">{props.data1.content}</p>
      </Nav.Link>
    </>
  );
}

export default Data;
