import {Container, Row, Col, Button, Nav} from "react-bootstrap";
import {useParams} from "react-router-dom";
import {ReactComponent as Star} from "./star.svg";
import {useState} from "react";
import {addItem} from "./store";
import {useDispatch} from "react-redux";
import {useNavigate} from "react-router-dom";
// 서브페이지 데이터 이미지
import info_item from "./Item_box/infomation";
import details_chart from "./Item_box/detail_chart";
import Footer from "./component/Footer";

function Detail(props) {
  let {id} = useParams();
  let [tab, setTab] = useState(0);
  let navigate = useNavigate();
  let dispatch = useDispatch(0);

  const [isActive, setIsActive] = useState(false);
  const handleClick = (event) => {
    setIsActive((current) => !current);
  };
  let selproduct = props.data1.find((x) => x.id == id);
  return (
    <Container className="per100">
      <Row>
        <Col>
          <div className="Col_padding">
            <img src={process.env.PUBLIC_URL + selproduct.imgUrl} width="50%" />
            <img src={process.env.PUBLIC_URL + selproduct.imgUrl2} width="50%" />
            <img src={process.env.PUBLIC_URL + selproduct.imgUrl3} width="33.32%" />
            <img src={process.env.PUBLIC_URL + selproduct.imgUrl4} width="33.32%" />
            <img src={process.env.PUBLIC_URL + selproduct.imgUrl5} width="33.32%" />
          </div>
          <Nav variant="tabs" defaultActiveKey="link0" className="tab_design">
            <Nav.Item>
              <Nav.Link
                onClick={() => {
                  setTab(0);
                }}
                eventKey="link0"
              >
                상품 정보
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link
                onClick={() => {
                  setTab(1);
                }}
                eventKey="link1"
              >
                세부 정보
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link
                onClick={() => {
                  setTab(2);
                }}
                eventKey="link2"
              >
                리뷰
              </Nav.Link>
            </Nav.Item>
          </Nav>
          <TabContent tab={tab} />
        </Col>
        <Col md={3} className="sidebar_wrap">
          <div className="pt-5 category_size">
            {selproduct.category}
            <div>
              {<Star style={{verticalAlign: "baseline"}} width="17px" fill="black"></Star>}
              {<Star style={{verticalAlign: "baseline"}} width="17px" fill="black"></Star>}
              {<Star style={{verticalAlign: "baseline"}} width="17px" fill="black"></Star>}
              {<Star style={{verticalAlign: "baseline"}} width="17px" fill="black"></Star>}
              {<Star style={{verticalAlign: "baseline"}} width="17px" fill="black"></Star>}
            </div>
          </div>
          <h4 className="py-3">{selproduct.title}</h4>
          <p className="font_price_style pb-2">{selproduct.price.toLocaleString("kr") + " 원"}</p>
          <p className="pb-4">{selproduct.content}</p>
          <div className="disdisa">
            <span
              className="buy_btn_num2"
              onClick={() => {
                navigate("/Cart/");
                dispatch(addItem({id: selproduct.id, name: selproduct.title, count: 1}));
              }}
            >
              장바구니 담기
            </span>
            <span className="wishlist_btn">
              <img src={isActive ? `${process.env.PUBLIC_URL}/img/icon/wishlist/wish_black.png` : `${process.env.PUBLIC_URL}/img/icon/wishlist/wish.png`} onClick={handleClick} />
            </span>
          </div>
        </Col>
      </Row>
      <Footer />
    </Container>
  );
  function TabContent(props) {
    let [data5, setData5] = useState(info_item);
    let [data6, setData6] = useState(details_chart);

    if (props.tab === 0) {
      return (
        <div className="infobox_wrap">
          <div className="infobox_right">
            <Col xs={12} md={8} lg={6}>
              <img src={process.env.PUBLIC_URL + data5[id].imgUrl} style={{width: "100%", height: "auto"}} />
            </Col>
          </div>
          <div className="infobox_left">
            <h2>{data5[id].info_title}</h2>
            <p>{data5[id].info_content}</p>
          </div>
        </div>
      );
    } else if (props.tab === 1) {
      return (
        <div className="infobox_wrap">
          <img src={process.env.PUBLIC_URL + data6[id].imgUrl} width="100%" />
        </div>
      );
    } else if (props.tab === 2) {
      return <div>내용2111111111111111111111111111111111111111</div>;
    }
  }
}
export default Detail;
