function Main_Data(props) {
  return (
    <>
      <img alt="common" src={process.env.PUBLIC_URL + props.data3.imgUrl} width="80%" />
      <p className="title_price3">{props.data3.price.toLocaleString("kr") + "원"}</p>
      <p className="title_txt3">{props.data3.title}</p>
      <p className="title_content3">{props.data3.content}</p>
    </>
  );
}

export default Main_Data;