import "./App.css";
import "@fortawesome/fontawesome-free/css/all.min.css";
// 네비,버튼 // 리액트
import React, {useState, useEffect} from "react";
import {Navbar, Nav, Container, ButtonGroup, Button, Row} from "react-bootstrap";
import axios from "axios";
import {useNavigate, Routes, Route, useLocation} from "react-router-dom";
// 비디오
import video from "./video/runing_video.mp4";
import video2 from "./video/Cool_Down.mp4";
//데이터(이미지)
import adidas_item from "./Item_box/adidas_item";
import teaser_arr from "./Item_box/teaser_item";
import Best_item from "./Item_box/Best_item";
import New_item from "./Item_box/New_item";
// 코드분리
import Mobile_Nav from "./component/Mobile_Nav";
import Navbar2 from "./component/Navbar2";
import New_item_slidess from "./component/New_item_slidess";
import Main_Data from "./component/Main_Data";
import Data from "./component/Data";
import Teaser from "./component/Teaser";
import Footer from "./component/Footer";
import Footer2 from "./component/Footer2";
import Detail from "./Detail";
import Cart from "./component/Cart";
import ScrollButton from "./component/ScrollButton";
// 라이브러리 Import Swiper React components
import {Swiper, SwiperSlide} from "swiper/react";
// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "./styles.css";
// import required modules
import {Pagination, Navigation} from "swiper";

function App() {
  let [data1, setdata1] = useState(adidas_item);
  let [res, setRes] = useState([0, 1, 2, 3, 4, 5, 6, 7, 8]);
  let navigate = useNavigate();
  const location = useLocation();
  const [showScrollButton, setShowScrollButton] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.pageYOffset > 300) {
        setShowScrollButton(true);
      } else {
        setShowScrollButton(false);
      }
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);
  return (
    <>
      <header>
        <Navbar bg="dark" variant="dark">
          <Container>
            <Nav className="Top_sub_Menu">
              <Nav.Link
                onClick={() => {
                  navigate("/Find_store");
                }}
              >
                매장찾기
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Customer");
                }}
              >
                고객센터
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Account");
                }}
              >
                계정
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Login");
                }}
              >
                로그인 &amp; 회원가입
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Cart");
                }}
              >
                <img src={process.env.PUBLIC_URL+"/img/icon/Menu/shopping_cart.png"} width="22px" />
              </Nav.Link>
            </Nav>
          </Container>
        </Navbar>
        <Navbar2 />
        <Mobile_Nav />
      </header>
      <Routes>
        <Route
          path="/"
          element={
            <div>
              <Main />
            </div>
          }
        />
        <Route path="/detail/:id" element={<Detail data1={data1} />} />
        <Route path="/cart" element={<Cart />} />
        <Route path="*" element={<div>없는 페이지입니다.</div>} />
      </Routes>
      {showScrollButton && <ScrollButton />}
    </>
  );
}

