import {useState, useEffect} from "react";

function Mobile_Nav() {
  const [NavActive, setNavActive] = useState(true);
  const [NavlistActive, setlistActive] = useState([true, true]); // 초기값 변경
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  const handleClick = (event) => {
    setNavActive((current) => !current);
    //   이벤트 전파 막기 (capturing)
    event.stopPropagation();
  };

  const ListClick = (index) => (event) => {
    // 함수를 고차함수로 변경
    setlistActive((current) => {
      const newListActive = [...current];
      newListActive[index] = !newListActive[index];
      return newListActive;
    });
    event.stopPropagation();
  };
  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  useEffect(() => {
    if (windowWidth <= 768) {
      setNavActive(false);
    } else {
      setNavActive(true);
    }
  }, [windowWidth]);
  return (
    <>
      <div>
        <button className="mobile_menu_close" onClick={handleClick}>
          <span className={NavActive ? "mobile_menubar" : "mobile_menubar active"} onClick={handleClick}></span>
        </button>
      </div>
      <div className={NavActive ? "nav_on_on nav_mobile_font" : "nav_on_off nav_mobile_font"}>
        <ul className="mobile_nav_margin">
          <li>
            <a onClick={ListClick(0)}>
              {/* index 값으로 0 전달 */}
              <h4>Men</h4>
            </a>
            <ul className={NavlistActive[0] ? "nav_list_on" : "nav_list_off"}>
              {/* index 값으로 0 전달 */}
              <li>
                <a href="#">신상품 전체보기</a>
              </li>
              <li>
                <a href="#">신발</a>
              </li>
              <li>
                <a href="#">의류</a>
              </li>
              <li>
                <a href="#">용품</a>
              </li>
              <li>
                <a href="#">스포츠</a>
              </li>
              <li>
                <a href="#">ACC</a>
              </li>
            </ul>
          </li>
          <li>
            <a onClick={ListClick(1)}>
              <h4>Women</h4>
            </a>
            <ul className={NavlistActive[1] ? "nav_list_on" : "nav_list_off"}>
              <li>
                <a href="#">신상품 전체보기</a>
              </li>
              <li>
                <a href="#">신발</a>
              </li>
              <li>
                <a href="#">의류</a>
              </li>
              <li>
                <a href="#">용품</a>
              </li>
              <li>
                <a href="#">스포츠</a>
              </li>
              <li>
                <a href="#">ACC</a>
              </li>
            </ul>
          </li>
          <li>
            <a onClick={ListClick(2)}>
              <h4>Kid</h4>
            </a>
            <ul className={NavlistActive[2] ? "nav_list_off" : "nav_list_on"}>
              <li>
                <a href="#">신상품 전체보기</a>
              </li>
              <li>
                <a href="#">신발</a>
              </li>
              <li>
                <a href="#">의류</a>
              </li>
              <li>
                <a href="#">용품</a>
              </li>
              <li>
                <a href="#">스포츠</a>
              </li>
              <li>
                <a href="#">ACC</a>
              </li>
            </ul>
          </li>
          <li>
            <a onClick={ListClick(3)}>
              <h4>SALE</h4>
            </a>
            <ul className={NavlistActive[3] ? "nav_list_off" : "nav_list_on"}>
              <li>
                <a href="#">신상품 전체보기</a>
              </li>
              <li>
                <a href="#">신발</a>
              </li>
              <li>
                <a href="#">의류</a>
              </li>
              <li>
                <a href="#">용품</a>
              </li>
              <li>
                <a href="#">스포츠</a>
              </li>
              <li>
                <a href="#">ACC</a>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </>
  );
}

export default Mobile_Nav;
