import React from "react";
import {Table} from "react-bootstrap";
import {useDispatch, useSelector} from "react-redux";
import {addCount, sortName} from "../store";
import {useState} from "react";

function Cart() {
  let state = useSelector((state) => state);
  let dispatch = useDispatch();

  const [isPurchased, setIsPurchased] = useState(false);
  const handlePurchase = () => {
    setIsPurchased(true);
  };
  const handleClose = () => {
    setIsPurchased(false);
    window.location.reload();
  };

  return (
    <div className="Cart_wrap">
      <div className="Cart_width">
        <h1>장바구니</h1>
        <Table>
          <thead>
            <tr>
              <th>#</th>
              <th>상품명</th>
              <th>수량</th>
              <th>변경하기</th>
            </tr>
          </thead>
          <tbody>
            {state.cart.map((v, i) => (
              <tr key={i}>
                <td>{state.cart[i].id}</td>
                <td>{state.cart[i].name}</td>
                <td>{state.cart[i].count}</td>
                <td>
                  <button onClick={() => dispatch(addCount(state.cart[i].id))}>+</button>
                  <button className="btn_remove" onClick={() => dispatch(addCount(state.cart[i].id) - 1)}>
                    -
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
        <div className="cart_btn_wrap">
          <button
            variant="outline=primary"
            onClick={() => {
              dispatch(sortName(state.cart.name));
            }}
          >
            이름순 정렬
          </button>
          <button onClick={handlePurchase} variant="outline=primary">
            구매하기
          </button>
          {isPurchased && (
            <div className="buy_modal">
              <p>구매가 완료되었습니다.</p>
              <button onClick={handleClose}>닫기</button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
export default Cart;
