function Teaser(props) {
  const fileName = props.data2.imgUrl.split(".");
  const fileExtension = fileName[fileName.length - 1];
  return (
    <>
      {fileExtension === "mp4" ? (
        <video alt="teaser_video" src={process.env.PUBLIC_URL + props.data2.imgUrl} width="100%" autoPlay="autoplay" muted="muted" loop="loop" playinline="playinline" />
      ) : (
        <img alt="teaser_img" src={process.env.PUBLIC_URL + props.data2.imgUrl} width="100%" />
      )}
      <p className="title_txt2">{props.data2.title}</p>
      <p className="title_content2">{props.data2.content}</p>
      <p className="title_price2">{props.data2.buy}</p>
    </>
  );
}

export default Teaser;