import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

function New_item_slidess(props) {
  return (
    <>
        <Col className="col-md-3">
          <div className="price_position ">
            <img alt="common" src={process.env.PUBLIC_URL + props.data4.imgUrl} />
            <p className="title_price4">{props.data4.price.toLocaleString("kr") + "원"}</p>
            <p className="title_txt3">{props.data4.title}</p>
            <p className="title_content3">{props.data4.content}</p>
          </div>
        </Col>
    </>
  );
}

export default New_item_slidess;
